body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.doom {
    color: #39c3f1;
    font-weight: bold;
}

.ui.table td {
    padding: 0.5em;
}

td.text-col {
    font-size: 14px;
}

.items-table tbody tr td:nth-child(2) {
    white-space: nowrap;
}

.items-table tbody tr td:nth-child(3) {
    padding: unset;
}

.items-table tbody tr td:nth-child(3) .ui.image {
    display: inline;
    display: initial;
    width: auto;
    height: 20px;
}

img.icon {
    width: auto;
    height: 18px;
    vertical-align: top;
}

.items-table img.icon.double-height {
    height: 36px;
    vertical-align: middle;
}

.items-table .source-col {
    white-space: nowrap;
}

text.item-head {
    font-size: 25px;
}

text.item-id,
text.item-cost {
    font-size: 40px;
}

text.item-count {
    font-size: 14px;
    font-family: 'Alice', serif;
}

text.item-text {
    font-size: 14px;
    font-family: 'Alice', serif;
}

svg.item-card {
    border-radius: 25px;
    margin-right: 10px;
    margin-bottom: 10px;
}

img.ui.image.icon {
    cursor: pointer;
    margin-left: 1em;
    display: inline-block;
}

img.ui.image.icon {
    width: auto;
    height: 20px;
    vertical-align: top;
    border: 1px solid limegreen;
    border-radius: 100%;
}

img.ui.image.icon.disabled {
    border: 1px solid transparent;
}

.pull-right {
    float: right;
}

.list-header-break {
    display: block !important;
}

.ui.radio.checked .icon {
    border-bottom: 2px solid #21ba45;
}

.ui.checkbox.checked .icon {
    border-bottom: 2px solid #21ba45;
}

.ui.horizontal.list {
    display: block;
}

.ui.text.red {
    color: #db2828 !important;
}

.ui.text.orange {
    color: #f2711c !important;
}

.ui.text.yellow {
    color: #fbbd08 !important;
}

.ui.text.olive {
    color: #b5cc18 !important;
}

.ui.text.green {
    color: #21ba45 !important;
}

.ui.text.teal {
    color: #00b5ad !important;
}

.ui.text.blue {
    color: #2185d0 !important;
}

.ui.text.violet {
    color: #6435c9 !important;
}

.ui.text.purple {
    color: #a333c8 !important;
}

.ui.text.pink {
    color: #e03997 !important;
}

.ui.text.brown {
    color: #a5673f !important;
}

.ui.text.grey {
    color: #767676 !important;
}

div.spoiler .ui.attached.segment {
    border: 1px dashed #db2828;
}

div.spoiler .ui.tabular.menu .item.active {
    border-left: 1px dashed #db2828;
    border-top: 1px dashed #db2828;
    border-right: 1px dashed #db2828;
}

div.spoiler .ui.tabular.menu {
    border-bottom: 1px dashed #db2828;
}

.item-card-wrapper {
    max-width: 20%;
    position: relative;
    display: inline-block;
}
img.item-card {
    width: 100%;
    height: auto;
    -webkit-filter: brightness(105%) contrast(90%);
            filter: brightness(105%) contrast(90%);
    border-radius: 15px;
    padding: 0 2px;
}
.item-card-wrapper .ui.toggle.checkbox label:before {
    background: rgba(255,255,255,0.5);
}

.item-card-wrapper .ui.toggle.checkbox {
    position: absolute;
}

.item-card-wrapper .ui.label.itemCost {
    position: absolute;
    right: 7px;
    top: 0px;
    color: white;
    border-radius: 5px;
    background: black;
}

.item-card-wrapper .ui.toggle.checkbox.i0 {
    left: 7px;
    top: 50px;
}
.item-card-wrapper .ui.toggle.checkbox.i1 {
    left: 7px;
    top: 85px;
}

.item-card-wrapper .ui.toggle.checkbox.i2 {
    right: 7px;
    top: 50px;
}
.item-card-wrapper .ui.toggle.checkbox.i3 {
    right: 7px;
    top: 85px;
}

.ui.form .fields.inline-break {
    display: inline;
    display: initial;
}

.ui.form .inline.inline-break.fields>label {
    display: block;
}
.ui.form .inline.inline-break.fields>.field {
    display: inline-block;
}

.item-summon {
    display: table-cell;
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 5px;
    font-size: smaller;
    color: #555;
}

.item-summon > div {
    display: inline-block;
    white-space: nowrap;
    padding: 0 2px;
    border-right: 1px solid rgba(0,0,0,.1);
}
.item-summon > div:last-child {
    border-right: none;
}

@media screen and (max-width: 768px) {

    .ui.table,
    .ui.basic.table,
    .items-table,
    .items-table tr,
    .items-table tr td {
        border: none;
    }

    .ui.table thead tr:first-child>th:first-child {
        border-radius: unset;
        border-bottom: unset;
    }

    .items-table tr {
        border: 2px solid #767676 !important;
        border-radius: 5px;
    }

    .items-table thead tr th:last-child {
        border-bottom: none !important;
    }
    .items-table tbody tr td:first-child,
    .items-table tbody tr td:last-child {
        border-top: none !important;
    }

    .items-table tbody tr:first-child td.cost-col,
    .items-table tbody tr:first-child td.use-col,
    .items-table tbody tr:first-child td.text-col {
        border-top: 1px solid rgba(0,0,0,.1);
    }

    .items-table tr {
        display: grid;
        grid-template-columns: repeat(10, 1fr);
        margin-bottom: 10px;
    }

    .items-table .ui.image {
        margin-top: 7px;
    }

    .items-table thead tr th.name-col,
    .items-table tbody tr td.name-col {
        grid-column: 3/7;
        font-size: 1.1em;
        font-weight: bold;
        white-space: normal;
        border-top: none;
    }

    .items-table thead tr th.slot-col,
    .items-table tbody tr td.slot-col {
        grid-row: 1;
        grid-column: 2;
        border-top: none;
    }

    .items-table thead tr th.cost-col,
    .items-table tbody tr td.cost-col {
        grid-row: 2;
        grid-column: 2;
        border-bottom: none;
    }

    .items-table thead tr th.use-col,
    .items-table tbody tr td.use-col {
        grid-column: 1;
        grid-row: 2;
        border-left: none !important;
        border-bottom: none;
    }

    .items-table thead tr th.text-col,
    .items-table tbody tr td.text-col {
        grid-column: 3/10;
        grid-row: 2;
        border-bottom: none;
    }

    .items-table thead tr th.source-col,
    .items-table tbody tr td.source-col {
        grid-column: 7/10;
        grid-row: 1;
        border-top: none;
    }

    .items-table thead tr th.store-inventory-col,
    .items-table tbody tr td.store-inventory-col {
        grid-column: 10;
        grid-row: 1/3;
    }
}

@media screen and (min-width: 415px) and (max-width: 768px) {
    .item-card-wrapper{
        max-width: 33.3333333333333%;
        padding: 0 2px;
    }
}

@media screen and (max-width: 414px) {

    .item-card-wrapper {
        max-width: 50%;
        padding-right: 2px;
    }

    .item-card-wrapper:nth-of-type(2n) {
        padding-right: 0;
        padding-left: 2px;
    }

    .items-table thead tr th.use-col,
    .items-table tbody tr td.use-col,
    .items-table thead tr th.text-col,
    .items-table tbody tr td.text-col {
        border-left: none !important;
        border-top: 1px solid rgba(0,0,0,.1);
    }

    .items-table thead tr th.slot-col,
    .items-table thead tr th.source-col {
        border-bottom: none !important;
    }

    .items-table tbody tr td.slot-col,
    .items-table tbody tr td.source-col {
        border-top: 1px solid rgba(0,0,0,.1) !important;
    }

    .items-table thead tr th.name-col,
    .items-table tbody tr td.name-col {
        grid-column: 2/10;
        font-size: 1.1em;
        font-weight: bold;
        white-space: normal;
        border-top: none;
    }

    .items-table thead tr th.slot-col,
    .items-table tbody tr td.slot-col {
        grid-row: 2;
        grid-column: 2;
        border-top: none;
    }

    .items-table thead tr th.cost-col,
    .items-table tbody tr td.cost-col {
        grid-row: 2;
        grid-column: 3;
        border-bottom: none;
    }

    .items-table thead tr th.use-col,
    .items-table tbody tr td.use-col {
        grid-column: 1;
        grid-row: 2;
        border-left: none !important;
        border-bottom: none;
    }

    .items-table thead tr th.text-col,
    .items-table tbody tr td.text-col {
        grid-column: 1/10;
        grid-row: 3;
        border-bottom: none;
    }

    .items-table thead tr th.source-col,
    .items-table tbody tr td.source-col {
        grid-column: 4/10;
        grid-row: 2;
        border-top: none;
    }

    .items-table thead tr th.store-inventory-col,
    .items-table tbody tr td.store-inventory-col {
        grid-column: 10;
        grid-row: 1/4;
    }

}

/* Red: #e81e25 */

.__xwc-hidden {
    display: none;
}

.__xwc-image-container {
    text-align: center;
    white-space: nowrap;
    min-width: 180px;
}

.__xwc-image-container > img {
    margin: 0 2px;
    vertical-align: middle;
}

.__xwc-container {
    cursor: help;
    border-bottom: 1px dashed #e81e25;
}

.__xwc-tooltip {
    position: absolute;
    background-color: #fff;
    border: 1px solid lightgrey;
    border-radius: 15px;
    padding: 5px;
    z-index: 9999999;
    transition: all 0.5s;
    opacity: 100;
    pointer-events: none;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: normal;
}

.__xwc-tooltip-hidden {
    opacity: 0;
}

.__xwc-powered-by {
    margin: 6px 2px;
    line-height: 16px;
    font-size: 10px;
    color: grey;
}

.__xwc-powered-by > img {
    vertical-align: middle;
    height: 16px;
    width: 16px;
}

.__xwc-loading-cube-grid {
    width: 40px;
    height: 40px;
    margin: 100px auto;
}

/* From http://tobiasahlin.com/spinkit/ */
.__xwc-loading-cube-grid .__xwc-loading-cube {
    width: 33%;
    height: 33%;
    background-color: #e81e25;
    float: left;
    -webkit-animation: __xwc-loading-cubeGridScaleDelay 1.3s infinite ease-in-out;
    animation: __xwc-loading-cubeGridScaleDelay 1.3s infinite ease-in-out;
}
.__xwc-loading-cube-grid .__xwc-loading-cube1 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s; }
.__xwc-loading-cube-grid .__xwc-loading-cube2 {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s; }
.__xwc-loading-cube-grid .__xwc-loading-cube3 {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s; }
.__xwc-loading-cube-grid .__xwc-loading-cube4 {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s; }
.__xwc-loading-cube-grid .__xwc-loading-cube5 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s; }
.__xwc-loading-cube-grid .__xwc-loading-cube6 {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s; }
.__xwc-loading-cube-grid .__xwc-loading-cube7 {
    -webkit-animation-delay: 0s;
    animation-delay: 0s; }
.__xwc-loading-cube-grid .__xwc-loading-cube8 {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s; }
.__xwc-loading-cube-grid .__xwc-loading-cube9 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s; }

@-webkit-keyframes __xwc-loading-cubeGridScaleDelay {
    0%, 70%, 100% {
        transform: scale3D(1, 1, 1);
    } 35% {
          transform: scale3D(0, 0, 1);
      }
}

@keyframes __xwc-loading-cubeGridScaleDelay {
    0%, 70%, 100% {
        transform: scale3D(1, 1, 1);
    } 35% {
          transform: scale3D(0, 0, 1);
      }
}

.map-container {
    display: flex;
}

.map {
    position: relative;
}

.map-tiles {
  min-width: 500px;
  min-height: 600px;
}

.map-spawn-point {
    position: absolute;
    width: 60px;
    height: 52px;
}

.map-spawn-id {
    width: 59px;
    text-align: center;
    line-height: 50px;
    color: black;
    position: absolute;
    top: 0;
}

.map-spawn-point img{
  max-width: 60px;
  max-height: 52px;
}

.rotated {
  transform-origin: center;
  transform: rotate(90deg);
}

.dungeon-card {
  max-width: 250px;
  max-height: 350px
}

.monster-tile {
  position: relative;
}

.monster-image {
  transform: scale(1.2);
  position: absolute;
}

.monster-image-info {
  width: 75px;
}

.map-grid {
  position: absolute;
  min-width: 500px;
  min-height: 600px;
  top: 0;
  left: 0;
}

.map-info svg g polygon {
  stroke: none;
  stroke-width: 0.2;
  transition: fill-opacity .5s
}

.rotated90 {
  transform: rotate(90deg);
}

svg {
}
/*
Russian blue pattern:
#7be3f6
#4499a9
#276a76
#133b43
#03090a
*/
svg g {
  fill: #4499a9;
  fill-opacity: 1;
}
svg g:hover {
  fill: #7be3f6;
  fill-opacity: 1;
}
svg g:hover text {
  fill-opacity: 1;
}

.map-grid svg g polygon {
  stroke: none;
  stroke-width: 0.2;
  transition: fill-opacity .5s
}
svg g text {
  font-size: 0.25em;
  fill: black;
  fill-opacity: 0.7;
  transition: fill-opacity .5s;
}
svg path {
  fill: none;
  stroke: #7be3f6;
  stroke-width: 0.2em;
  stroke-opacity: 0.7;
  stroke-linecap: round;
  stroke-linejoin: round;
}
